import logo from './logo.svg';
import './bt/dist/css/bootstrap.min.css';
import React from 'react';
import Camera from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import { geolocated } from "react-geolocated";
import axios from 'axios';
import { render } from '@testing-library/react';
import { location } from 'globalthis/implementation';
import swal from 'sweetalert';

var globalThis = {
  ck: null
}

var postLink = 'localhost';

var geolocation = require('geolocation')

class NewComponent extends React.Component {

  constructor(){
    super();
  }

  render() {
    var c = globalThis.image;
    globalThis.ck = null;
    return (
      <div {...this.props}>
        <center>
          <img width={200} src={globalThis.image}></img>
          <div>
            <p>coordinat maps</p>
            <p>{globalThis.latitude}</p>
            <p>{globalThis.longitude}</p>
          </div>
        </center>
      </div>
    );
  }  
}

class App extends React.Component {
  constructor(){
    super();
    this.state = {
      clicked : false
    }
    this.handleTakePhoto = this.handleTakePhoto.bind(this);
  }

  handleClick(a) {
    if(globalThis.ck != null){
      globalThis.moment = a;
      this.setState({
        clicked: true
      });
    }else{
      setTimeout(
        function() {
            this.handleClick();
        }
        .bind(this),
        1000
      );

    }
  }

  async handleTakePhoto (dataUri) {
    var ck = null;
    var lock = await geolocation.getCurrentPosition(function (err, position) {
      if (err) throw err
      ck = {
        image: dataUri,
        latitude: position.coords.latitude,
        longitude: position.coords.longitude
      }
      globalThis.latitude = position.coords.latitude;
      globalThis.longitude = position.coords.longitude;
      globalThis.image = dataUri;
      globalThis.data = position;
      globalThis.ck = ck;
      axios.post(location.origin+'/api/absensi/post',{
        data:{
          image: dataUri,
          latitude: position.coords.latitude,
          longitude: position.coords.longitude
        }
      })
      .then(res => {
          console.log('success');
          swal("Warning!", "Absensi berhasil, silahkan kembali ke menu", "success");
      }, this)
      .catch(err => {
          console.log('warning');
          swal("Warning!", "Anda gagal absensi, pastikan anda terkoneksi insternet dan coba kembali", "warning");
      },this)
    })
    this.handleClick(ck);
  }
  render(){
    geolocation.getCurrentPosition(function (err, position) {
      if (err) throw err
      console.log(position)
    })
    return (
      <div className="container">
        <div className="text-center">
          <h1>Absensi</h1>
          <p>Pastikan perangkat kamera anda tersedia</p>
        </div>
         <Camera
            onTakePhoto = { (dataUri) => { this.handleTakePhoto(dataUri); } }
          />
          {this.state.clicked ? <NewComponent /> : null}
      </div>
    );
  }
}
export default App;
